                <div class="md:w-1/3"></div>
                <div class="md:w-2/3">
                  <button
                    class="admin_btn"
                    type="button"
                    @click="
                      updateProjectWhitelistPrice(data.projectId, data.price)
                    "
                  >
                    Change
                  </button>
                </div>
              </div>
            </form>
          </div>
        </li>
        <li class="m-3">
          <button
            class="tw_btn2"
            @click="
              show.updateProjectSecondaryMarketRoyaltyPercentage =
                !show.updateProjectSecondaryMarketRoyaltyPercentage
            "
          >
            Update Royalty Percentage
          </button>
          <div v-if="show.updateProjectSecondaryMarketRoyaltyPercentage">
            <form class="w-full max-w-sm">
              <h1 class="admin_txt">Update Royalty Percentage</h1>
              <div class="md:flex md:items-center mb-6">
                <div class="md:w-1/3">
                  <label class="admin_lb" for="inline-full-name">
                    Royalty
                  </label>
                </div>
                <div class="md:w-2/3">
                  <input
                    class="admin_input"
                    id="inline-full-name"
                    type="number"
                    placeholder="Enter Royalty Percentage"
                    v-model="data.percentage"
                  />
                </div>
              </div>

              <div class="md:flex md:items-center">
                <div class="md:w-1/3"></div>
                <div class="md:w-2/3">
                  <button
                    class="admin_btn"
                    type="button"
                    @click="
                      updateProjectSecondaryMarketRoyaltyPercentage(
                        data.projectId,
                        data.price
                      )
                    "
                  >
                    Change
                  </button>
                </div>
              </div>
            </form>
          </div>
        </li>
      </ul>
    </div>

    <div v-if="showFail">
      <div
        class="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"
      ></div>

      <div class="fixed z-10 inset-0 overflow-y-auto">
        <div
          class="flex items-end sm:items-center justify-center min-h-full p-4 text-center sm:p-0"
        >
          <div
            class="relative bg-white dark:bg-black rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:max-w-lg sm:w-full"
          >
            <div class="bg-white dark:bg-black px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
              <div class="sm:flex sm:items-start">
                <div
                  class="mx-auto flex-shrink-0 flex items-center justify-center h-12 w-12 rounded-full bg-red-100 sm:mx-0 sm:h-10 sm:w-10 text-red-500"
                >
                  <i class="fa-solid fa-triangle-exclamation"></i>
                </div>
                <div class="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                  <h3
                    class="text-lg leading-6 font-medium text-black dark:text-white uppercase"
                    id="modal-title"
                  >
                    fail
                  </h3>
                  <div class="mt-2">
                    <p
                      class="text-sm text-gray-500 dark:text-gray-200 overflow-auto"
                    >
                      시스템 오류 입니다. 에러 코드:{{ failMsg }}
                    </p>
                  </div>
                </div>
              </div>
            </div>
            <div
              class="bg-white dark:bg-black px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse"
            >
              <button
                type="button"
                @click="showFail = false"
                class="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-red-600 text-base font-medium text-white hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 sm:ml-3 sm:w-auto sm:text-sm"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="showSuccess">
      <div
        class="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"
      ></div>

      <div class="fixed z-10 inset-0 overflow-y-auto">
        <div
          class="flex items-end sm:items-center justify-center min-h-full p-4 text-center sm:p-0"
        >
          <div
            class="relative bg-white dark:bg-black rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:max-w-lg sm:w-full"
          >
            <div class="bg-white dark:bg-black px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
              <div class="sm:flex sm:items-start">
                <div
                  class="mx-auto flex-shrink-0 flex items-center justify-center h-12 w-12 rounded-full bg-green-100 sm:mx-0 sm:h-10 sm:w-10 text-green-500"
                >
                  <i class="fa-solid fa-check"></i>
                </div>
                <div class="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                  <h3
                    class="text-lg leading-6 font-medium text-black dark:text-white uppercase"
                    id="modal-title"
                  >
                    success
                  </h3>
                  <div class="mt-2">
                    <p class="text-sm text-gray-500 dark:text-gray-200">
                      Your request has been successfully executed.
                    </p>
                  </div>
                </div>
              </div>
            </div>
            <div
              class="bg-white dark:bg-black px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse"
            >
              <button
                type="button"
                @click="showSuccess = false"
                class="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-green-600 text-base font-medium text-white hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 sm:ml-3 sm:w-auto sm:text-sm"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, reactive, ref } from "vue";
import { CONTRACT_ABI, CONTRACT_ADDRESS } from "@/assets/artKonnection.js";
import Caver from "caver-js";
import { useRoute } from "vue-router";

export default {
  setup() {
    let caver = null;
    let contract = null;
    let showFail = ref(false);
    let showSuccess = ref(false);
    let failMsg = ref("error");

    let data = reactive({
      price: 0,
      projectName: "",
      artistAddress: "",
      projectId: 0,
      limit: 0,
      blockNumber: 0,
      name: "",
      url: "",
      description: "",
      license: "",
      invocation: 0,
      percentage: 0,
      website: "",
    });

    let show = reactive({
      updateProjectSecondaryMarketRoyaltyPercentage: false,
      toggleWhitelistMintEnabled: false,
      toggleProjectIsActive: false,
      toggleProjectIsLocked: false,
      updateProjectWebsite: false,
      updateMintLimitPerBlock: false,
      updateMintStartBlockNumber: false,
      updateProjectArtistAddress: false,
      updateProjectArtistName: false,
      updateProjectBaseIpfsURI: false,
      updateProjectDescription: false,
      updateProjectLicense: false,
      updateProjectMaxInvocations: false,
      updateProjectName: false,
      updateProjectPricePerTokenInPeb: false,
      updateProjectWhitelistPrice: false,
    });

    onMounted(() => {
      const route = useRoute();
      const _projectId = Number(route.params.projectid);
      data.projectId = _projectId;
      caver = new Caver(window.klaytn);
      contract = new caver.klay.Contract(CONTRACT_ABI, CONTRACT_ADDRESS);
      _verify(_projectId);
      _getProjectData(_projectId);
      _getCurrentBlock();
      _getTokenLogs(_projectId);
    });

    let currentBlockNumber = ref("");
    const _getCurrentBlock = () => {
      setInterval(() => {
        caver.rpc.klay.getBlockNumber().then((res) => {
          const _currentBlockNumber = caver.utils.hexToNumberString(res);
          currentBlockNumber.value = _currentBlockNumber;
        });
      }, 1000);
    };

    let showdata = reactive({
      MintLimitPerBlock: 0,
      MintStartBlockNumber: 0,
      projectName: "",
      artistName: "",
      projectDescription: "",
      projectWebsite: "",
      projectLicense: "",
      artistAddress: "",
      price: "",
      whitelistPrice: "",
      invocation: "",
      maxInvocations: "",
      active: "",
      url: "",
      imageurl: "",
      whitelistMintActive: "",
    });

    const _verify = async (_projectId) => {
      await contract.methods
        .projectIdToArtistAddress(_projectId)
        .call()
        .then((result) => {
          if (
            result.toUpperCase() == window.klaytn.selectedAddress.toUpperCase()
          ) {
            return;
          } else {
            window.location.replace(process.env.VUE_APP_CILENT_URL);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    };

    const _getProjectData = async (_projectId) => {
      await contract.methods
        .mintingInformation(_projectId)
        .call()
        .then((res) => {
          showdata.MintLimitPerBlock = res[1];
          showdata.MintStartBlockNumber[2];
        })
        .catch((err) => {
          console.log(err);
        });
      await contract.methods
        .projectDetails(_projectId)
        .call()
        .then((res) => {
          showdata.projectName = res.projectName;
          showdata.artistName = res.artist;
          showdata.projectDescription = res.description;
          showdata.projectWebsite = res.website;
          showdata.projectLicense = res.license;
          showdata.imageurl = `${process.env.VUE_APP_SERVER_URL}/coverassets/${res.projectName}`;
        })
        .catch((err) => {
          console.log(err);
        });
      await contract.methods
        .projectTokenInfo(_projectId)
        .call()
        .then((res) => {
          showdata.artistAddress = res.artistAddress;
          showdata.price = caver.utils.fromPeb(res.pricePerTokenInPeb, "KLAY");
          showdata.whitelistPrice = caver.utils.fromPeb(
            res.whitelistPricePerTokenInPeb,
            "KLAY"
          );
          showdata.invocation = res.invocations;
          showdata.maxInvocations = res.maxInvocations;
          showdata.active = res.active;
          showdata.whitelistMintActive = res.whitelistMintActive;
        })
        .catch((err) => {
          console.log(err);
        });
      await contract.methods
        .projectURIInfo(_projectId)
        .call()
        .then((res) => {
          showdata.url = res;
        })
        .catch((err) => {
          console.log(err);
        });
      await contract.methods
        .royaltyInfo(1, 100)
        .call()
        .then((res) => {
          showdata.royalty = (100 * res[1]) / 100;
        });
    };

    const toggleWhitelistMintEnabled = async (_projectId) => {
      await contract.methods
        .toggleWhitelistMintEnabled(Number(_projectId))
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };
    const updateProjectSecondaryMarketRoyaltyPercentage = async (
      _projectId,
      _royalty
    ) => {
      await contract.methods
        .updateProjectSecondaryMarketRoyaltyPercentage(
          Number(_projectId),
          Number(_royalty)
        )
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };

    const toggleProjectIsActive = async (_projectId) => {
      await contract.methods
        .toggleProjectIsActive(Number(_projectId))
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };
    const toggleProjectIsLocked = async (_projectId) => {
      await contract.methods
        .toggleProjectIsLocked(Number(_projectId))
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };

    const updateMintLimitPerBlock = async (_projectId, _limit) => {
      await contract.methods
        .updateMintLimitPerBlock(Number(_projectId), Number(_limit))
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };
    const updateMintStartBlockNumber = async (_projectId, _blockNumber) => {
      await contract.methods
        .updateMintStartBlockNumber(
          Number(_projectId),
          caver.utils.toBN(_blockNumber)
        )
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };
    const updateProjectArtistAddress = async (_projectId, _artistAddress) => {
      await contract.methods
        .updateProjectArtistAddress(Number(_projectId), _artistAddress)
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };
    const updateProjectArtistName = async (_projectId, _projectArtistName) => {
      await contract.methods
        .updateProjectArtistName(Number(_projectId), String(_projectArtistName))
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };
    const updateProjectBaseIpfsURI = async (
      _projectId,
      _projectBaseIpfsURI
    ) => {
      await contract.methods
        .updateProjectBaseIpfsURI(
          Number(_projectId),
          String(_projectBaseIpfsURI)
        )
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };
    const updateProjectDescription = async (
      _projectId,
      _projectDescription
    ) => {
      await contract.methods
        .updateProjectDescription(
          Number(_projectId),
          String(_projectDescription)
        )
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };
    const updateProjectLicense = async (_projectId, _projectLicense) => {
      await contract.methods
        .updateProjectLicense(Number(_projectId), String(_projectLicense))
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };
    const updateProjectMaxInvocations = async (_projectId, _maxInvocations) => {
      await contract.methods
        .updateProjectMaxInvocations(
          Number(_projectId),
          caver.utils.toBN(_maxInvocations)
        )
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };
    const updateProjectName = async (_projectId, _projectName) => {
      await contract.methods
        .updateProjectName(Number(_projectId), String(_projectName))
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };
    const updateProjectPricePerTokenInPeb = async (
      _projectId,
      _pricePerTokenInPeb
    ) => {
      const peb = caver.utils.toPeb(_pricePerTokenInPeb, "KLAY");
      await contract.methods
        .updateProjectPricePerTokenInPeb(
          Number(_projectId),
          caver.utils.toBN(peb)
        )
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };
    const updateProjectWhitelistPrice = async (
      _projectId,
      _pricePerTokenInPeb
    ) => {
      const peb = caver.utils.toPeb(_pricePerTokenInPeb, "KLAY");
      await contract.methods
        .updateProjectWhitelistPrice(Number(_projectId), caver.utils.toBN(peb))
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };
    const updateProjectWebsite = async (_projectId, _projectWebsite) => {
      await contract.methods
        .updateProjectWebsite(Number(_projectId), String(_projectWebsite))
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then(() => {
          showSuccess.value = true;
        })
        .catch((err) => {
          failMsg.value = err;
          showFail.value = true;
        });
    };

    let history = ref();

    const _getTokenLogs = (_projectId) => {
      contract
        .getPastEvents("Mint", {
          filter: { _projectId: [_projectId] }, // Using an array means OR: e.g. 20 or 23
          fromBlock: 90000000,
          toBlock: "latest",
        })
        .then((events) => {
          if (
            events.length > 0 &&
            events !== undefined &&
            events.length !== undefined
          ) {
            history.value = events.reverse();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    };
    return {
      toggleWhitelistMintEnabled,
      toggleProjectIsActive,
      toggleProjectIsLocked,
      updateMintLimitPerBlock,
      updateMintStartBlockNumber,
      updateProjectArtistAddress,
      updateProjectArtistName,
      updateProjectBaseIpfsURI,
      updateProjectDescription,
      updateProjectLicense,
      updateProjectMaxInvocations,
      updateProjectName,
      updateProjectPricePerTokenInPeb,
      updateProjectSecondaryMarketRoyaltyPercentage,
      updateProjectWebsite,
      updateProjectWhitelistPrice,
      data,
      show,
      showdata,
      failMsg,
      currentBlockNumber,
      history,
      showFail,
      showSuccess,
    };
  },
};
</script>
<style scoped>
li {
  @apply grid grid-cols-1 justify-items-center;
}
.admin_input {
  @apply bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-gray-500;
}
.admin_btn {
  @apply shadow bg-transparent border-2 border-black dark:border-white text-black dark:text-white font-bold py-2 px-4 rounded;
}
.admin_txt {
  @apply text-green-500 font-bold;
}
.admin_lb {
  @apply block text-gray-500 font-bold md:text-right mb-1 md:mb-0 pr-4;
}
.tw_btn2 {
  @apply px-4 py-1 w-1/2 rounded-full h-10 bg-transparent border-2 border-black text-black dark:text-white  dark:border-white;
}
</style>
